/* eslint-disable @next/next/no-img-element */
'use client';
import Footer from '@/components/footer/page';
import Header from '@/components/header/page';
import PaginationComponent from '@/components/pagination/page';
import Sidebar from '@/components/sidebar/page';
import React, { useState } from 'react';


export default function AllProducts() {
  const products = [
    {
      id: 1,
      name: 'Wireless Earbuds',
      price: '$99.00',
      category: 'Electronics',
      stock: 120,
      status: 'Available',
      image: 'https://images.unsplash.com/photo-1590658268037-6bf12165a8df?w=500&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8ZWFyYnVkc3xlbnwwfHwwfHx8MA%3D%3D',
    },
    {
      id: 2,
      name: 'Running Shoes',
      price: '$75.00',
      category: 'Footwear',
      stock: 50,
      status: 'Out of Stock',
      image: 'data:image/jpeg;base64,',
    },
    {
      id: 3,
      name: 'Smartwatch',
      price: '$199.00',
      category: 'Accessories',
      stock: 30,
      status: 'Available',
      image: 'https://via.placeholder.com/100x100',
    },
    {
      id: 4,
      name: 'Gaming Laptop',
      price: '$1,499.00',
      category: 'Computers',
      stock: 10,
      status: 'Low Stock',
      image: 'https://via.placeholder.com/100x100',
    },
    {
      id: 5,
      name: 'Yoga Mat',
      price: '$25.00',
      category: 'Fitness',
      stock: 0,
      status: 'Out of Stock',
      image: 'https://via.placeholder.com/100x100',
    },
    {
      id: 6,
      name: 'Bluetooth Speaker',
      price: '$45.00',
      category: 'Audio',
      stock: 200,
      status: 'Available',
      image: 'https://via.placeholder.com/100x100',
    },

  ];

  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 20;

  const totalPages = Math.ceil(products.length / itemsPerPage);

  const handlePageChange = (event: React.ChangeEvent<unknown>, page: number) => {
    setCurrentPage(page);
  };

  // Calculate products for the current page
  const startIndex = (currentPage - 1) * itemsPerPage;
  const currentProducts = products.slice(startIndex, startIndex + itemsPerPage);

  const getStatusBadge = (status: string) => {
    const colors: Record<string, string> = {
      Available: 'bg-green-100 text-green-700',
      'Out of Stock': 'bg-red-100 text-red-700',
      'Low Stock': 'bg-yellow-100 text-yellow-700',
    };

    return (
      <span className={`px-3 py-1 text-sm font-medium rounded-full ${colors[status]}`}>
        {status}
      </span>
    );
  };

  return (
    <div className="flex flex-col min-h-screen">
      <div className='flex flex-grow'>
        <Sidebar/>
        <div className="flex flex-col flex-grow">
          <Header/>
        <section className="p-6 bg-gray-50 rounded-lg shadow-lg">
      <h2 className="text-2xl font-semibold text-gray-800 mb-6">All Products</h2>
      <div className="hidden md:block">
        <table className="w-full table-auto border-collapse bg-white rounded-lg shadow">
          <thead className="bg-gray-100">
            <tr className="text-left text-gray-600 text-sm">
              <th className="px-6 py-3 font-medium">Image</th>
              <th className="px-6 py-3 font-medium">Product ID</th>
              <th className="px-6 py-3 font-medium">Name</th>
              <th className="px-6 py-3 font-medium">Price</th>
              <th className="px-6 py-3 font-medium">Category</th>
              <th className="px-6 py-3 font-medium">Stock</th>
              <th className="px-6 py-3 font-medium">Status</th>
            </tr>
          </thead>
          <tbody>
            {currentProducts.map((product, index) => (
              <tr
                key={product.id}
                className={`${
                  index % 2 === 0 ? 'bg-gray-50' : 'bg-white'
                } hover:bg-gray-100 transition-colors`}
              >
                <td className="px-6 py-4">
                  <img
                    src={product.image}
                    alt={product.name}
                    className="w-16 h-16 object-cover rounded-lg"
                  />
                </td>
                <td className="px-6 py-4 text-sm font-semibold text-gray-700">#{product.id}</td>
                <td className="px-6 py-4 text-sm text-gray-600">{product.name}</td>
                <td className="px-6 py-4 text-sm font-bold text-gray-800">{product.price}</td>
                <td className="px-6 py-4 text-sm text-gray-500">{product.category}</td>
                <td className="px-6 py-4 text-sm text-gray-600">{product.stock}</td>
                <td className="px-6 py-4">{getStatusBadge(product.status)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="md:hidden">
        {currentProducts.map((product) => (
          <div
            key={product.id}
            className="mb-4 bg-white rounded-lg shadow p-4 border border-gray-200"
          >
            <div className="flex justify-between items-center mb-2">
              <h3 className="text-lg font-semibold text-gray-700">Product #{product.id}</h3>
              {getStatusBadge(product.status)}
            </div>
            <img
              src={product.image}
              alt={product.name}
              className="w-24 h-24 object-cover rounded-lg mb-4"
            />
            <p className="text-sm text-gray-600">
              <span className="font-medium">Name:</span> {product.name}
            </p>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Price:</span> {product.price}
            </p>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Category:</span> {product.category}
            </p>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Stock:</span> {product.stock}
            </p>
          </div>
        ))}
      </div>
      <PaginationComponent
        currentPage={currentPage}
        totalPages={totalPages}
        onPageChange={handlePageChange}
      />
      </section>
          </div>
      </div>
      <Footer/>
    </div>
   
  );
}
